			</ul>
		</section>
		<footer>
			<button>13</button>
		</footer>
	</main>
</template>
<script>
export default{
	data(){
		return{
			items:[{'anwser':'','question':''},{'anwser':'','question':''}]
		}
	}
}
</script>
<style lang="scss" scoped>
	@import '../../assets/css/syxq/quest.scss'
</style>


