<template>
  <div class="myOrderForm">
    <mOFheader title=""></mOFheader>
    <nav ref="nav">
      <span :class="nav === ''?'active':''" @click="change($event)"></span>
      <span :class="nav === ''?'active':''" @click="change($event)"></span>
      <span :class="nav === ''?'active':''" @click="change($event)"></span>
      <span :class="nav === ''?'active':''" @click="change($event)"></span>
    </nav>
    <section id="index-scroll" ref="wrapper">
      <ul>
        <div class="refresh">
          <img src="../assets/images/myOrderForm/loading.png" alt=""><span>{{top}}...</span>
        </div>
        <li v-for="item in list" v-if="item.status === nav || nav === ''">
          <h2 v-if="item.status === ''">
            <span class="time time0">{{item.time}}</span>
            <span class="status0">{{item.status}}</span>
          </h2>
          <h2 v-if="item.status === ''">
            <span class="time time1">{{item.time}}</span>
            <span class="status1">{{item.status}}</span>
          </h2>
          <h2 v-if="item.status === ''">
            <span class="time time0">{{item.time}}</span>
            <span class="status0">{{item.status}}</span>
          </h2>
          <h2 v-if="item.status === ''">
            <span class="time time2">{{item.time}}</span>
            <span class="status2">{{item.status}}</span>
          </h2>
          <section class="detail">
            <img src="../assets/images/myOrderForm/ru.png" alt="">
            <div>
              <p> <span>{{item.userName}}</span></p>
              <p><span>{{item.fuwu}}</span></p>
            </div>
          </section>
          <section class="btn">
            <p></p>
            <div v-if="item.status === ''" class="btn1"></div>
            <div v-if="item.status === ''" class="btn0"></div>
          </section>
        </li>
      </ul>
    </section>
  </div>
</template>

<script>
  import mOFheader from '../components/header.vue'
  import BScroll from 'better-scroll'
  import Vue from 'vue'
  export default {
    name: 'myOrderForm',
    data() {
      return {
        list: [{
          time: '2017-7-21  16 : 10',
          status: '',
          userName: 'kenshin',
          fuwu: ''
        }, {
          time: '2017-7-21  16 : 10',
          status: '',
          userName: 'kenshin',
          fuwu: ''
        }, {
          time: '2017-7-21  16 : 10',
          status: '',
          userName: 'kenshin',
          fuwu: ''
        }, {
          time: '2017-7-21  16 : 10',
          status: '',
          userName: 'kenshin',
          fuwu: ''
        }],
        startY: -50,
        top: '',
        nav: ''
      }
    },
    components: {
      mOFheader
    },
    mounted: function() {
      this.listScroll()
    },
    methods: {
      listScroll: function() {
        var that = this
        Vue.nextTick(() => {
          that.wrapper = new BScroll(that.$refs.wrapper, {
            startY: that.startY,
            click: true
          })
          if (that.wrapper.wrapperHeight < that.wrapper.scrollerHeight) {
            that.add = true
          }
          that.top = ''
          that.wrapper.on('scrollEnd', (pos) => {
            if (pos.y === 0) {
              that.pageNumber = 1
              that.startY = -50
            } else if (pos.y === that.wrapper.maxScrollY) {
              that.startY = that.wrapper.maxScrollY
              that.pageNumber++
            }
          })
        })
      },
      change: function (e) {
        this.nav = e.target.innerText
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/myOrderForm.scss';
</style>



